.expander-content {
    position: relative;
    width: 100vw;
    left: calc(-50vw + 50%);
    height: 100%;
    margin: 30px 0; }

.expander-content-main {
    // margin: 0 20px
    background: lighten($accent-color, 10%);
    text-align: center;

    @media screen and (max-width: 760px) {
        margin: 0; }

    .expander-text {
        max-width: 1300px;
        margin: 0px auto;
        padding: 20px 0;

        p {
            margin: 0 20px 30px 20px; }
        a {
            color: $selection-color;
            border-bottom: none; }

        a:hover, a:focus {
            color: $text-color;
            border-bottom: 2px dashed $text-color; } }


    .container-photos {
        margin: 20px 0; } }

.row-photos {
    width: 100%;
    margin: 0px auto;
    // padding: 20px 0 20px
    @include clearfix;

    .photo {
        display: block;
        width: 33.33%;
        width: calc(100% / 3);
        float: left;
        box-sizing: border-box;
        position: relative;
        padding-top: 25%;
        font-size: 16px;
        text-align: left;

        @media screen and (max-width: 760px) {
            width: 100%;
            padding-top: 75%; } }

    .photo-overlay {
        position: absolute;
        bottom: 0;
        background: #FFF;
        padding: 0 5px 2px 5px; }

