.landingpage {
    //background-color: red
    text-align: center;

    .landing-header {

        position: relative;
        margin-top: 25vh;

        h1 {
            @include animation(FadeOut 2.5s forwards);

            color: $accent-color;

            @media screen and (max-width: 950px) {
                font-size: 400%; } }

        .landing-logo {

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .logo {
                //background: yellow
                @include size(85px);
                display: inline-block;
                border-radius: 50%;
                border-bottom: none;
                @include transition(1s);
                @include transform(rotate(0deg)); }

            .logo:hover {
                @include transform(rotate(360deg)); } } } }
