header {

    padding-bottom: 40px;

    max-width: 750px;
    margin: 0 auto 40px;

    @media screen and (max-width: 1050px) {
        margin: 0 auto;
        padding-bottom: 20px; }

    .landing-header {

        position: relative;
        margin-top: 25vh;

        h1 {
            @include animation(FadeOut 2.5s forwards);

            color: $accent-color;

            @media screen and (max-width: 950px) {
                font-size: 400%; } } }

    .sprite {
        width: 48px;
        height: 48px;
        position: absolute;
        top: 20px;
        background: none;
        border: none; }


    .midden {

        text-align: center;

        .landing-title {
            //background: silver
            display: inline-block;
            padding: 20px;

            @media screen and (max-width: 520px) {
                display: none; } }

        .logo {
            @include size(85px);
            display: inline-block;
            position: relative;
            top: 25px;
            border-radius: 50%;
            border-bottom: none;
            background: none;
            @include transition(1s);
            @include transform(rotate(0deg)); }

        .logo:hover {
            @include transform(rotate(360deg));
 } }            // background: lighten($accent-color, 10%)


    .menu {

        //background: gray
        position: relative;
        text-align: center;
        padding: 32px 0 0 0;
        top: 0;
        left: 0;
        font-size: 0.9em;

        @media screen and (max-width: 1050px) {
            position: relative;
            text-align: center;
            padding: 32px 0 0 0;
            top: 0;
            left: 0; }

        .menu-items {

            margin-bottom: 10px;
            display: inline-block;
            margin: 0 15px 0 0;

            @media screen and (max-width: 1050px) {
                display: inline-block;
                margin: 0 15px 0 0; } } }

    .social-header {

        position: fixed;
        top: 35px;
        right: 35px;
        z-index: 99;

        @media screen and (max-width: 1050px) {
            position: relative;
            text-align: center;
            padding: 32px 0 0 0;
            top: 0;
            right: 0; }

        .social-icon {
            @include size(30px);
            border-radius: 20%;
            padding: 0;
            margin: 0 auto 10px;
            display: block;
            background: rgba(255, 255, 255, .6);
            border-bottom: none;

            @media screen and (max-width: 1050px) {
                display: inline-block; } }

        .social-icon:hover {
            background: lighten($accent-color, 10%); }

        .social-icon.email {
            @include size(35px);

            @media screen and (max-width: 1050px) {
                position: relative;
                top: 2.5px; } } } }

